import React, { useState } from "react";
import { useTranslation } from "react-i18next";

const Habilidades = () => {
  const { t } = useTranslation();
  const [abaAtiva, setAbaAtiva] = useState("tecnologias");

  const tecnologias = [
    { nome: "JavaScript", slug: "javascript" },
    { nome: "React", slug: "react" },
    { nome: "TailwindCSS", slug: "tailwind" },
    { nome: "Python", slug: "python" },
    { nome: "Flask", slug: "flask" },
    { nome: "HTML", slug: "html" },
    { nome: "CSS", slug: "css" },
    { nome: "Bootstrap", slug: "bootstrap" },
    { nome: "PostgreSQL", slug: "postgres" },
  ];
  const ferramentas = [
    { nome: "Git", slug: "git" },
    { nome: "GitHub", slug: "github" },
    { nome: "Selenium", slug: "selenium" },
    { nome: "Postman", slug: "postman" },
    { nome: "Vite", slug: "vite" },
    { nome: "Docker", slug: "docker" },
  ];
  const outros = t("skills.outros", { returnObjects: true });
  const seguranca = t("skills.seguranca", { returnObjects: true });

  return (
    <>
      <section id="habilidades" className="bg-white text-black mt-0 sm:-mt-1 py-16 sm:py-24 px-4 sm:px-6 relative z-10">
        <h2 className="text-center text-4xl font-extrabold italic mb-8 text-black-400">
          <span>{t("skills.title")}</span>
        </h2>

        <div className="bg-[#a4a3a3] text-white p-6 rounded-xl min-h-[250px] shadow-md mx-auto w-full max-w-[95%] sm:max-w-[90%]">
          {/* Abas */}
          <div className="flex flex-wrap justify-center gap-2 sm:gap-3 mb-6 w-full overflow-visible">
            {["tecnologias", "ferramentas", "outros", "seguranca"].map((aba) => (
              <button
                key={aba}
                onClick={() => setAbaAtiva(aba)}
                className={`px-4 py-2 rounded-md transition font-medium ${abaAtiva === aba
                    ? "bg-purple-600 text-white"
                    : "bg-[#3a3a3a] text-gray-300 hover:bg-purple-500 hover:text-white"
                  }`}
              >
                {t(`skills.tabs.${aba}`)}
              </button>
            ))}
          </div>

          {abaAtiva === "tecnologias" && (
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4 sm:gap-6 place-items-center">
              {tecnologias.map((tec) => (
                <div key={tec.nome} className="flex flex-col items-center">
                  <img
                    src={`https://skillicons.dev/icons?i=${tec.slug}`}
                    alt={tec.nome}
                    className="w-16 h-16 object-contain mb-2"
                    loading="lazy"
                  />
                  <span className="text-sm">{tec.nome}</span>
                </div>
              ))}
            </div>
          )}

          {abaAtiva === "ferramentas" && (
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-6 place-items-center">
              {ferramentas.map((f) => (
                <div key={f.nome} className="flex flex-col items-center">
                  <img
                    src={`https://skillicons.dev/icons?i=${f.slug}`}
                    alt={f.nome}
                    className="w-16 h-16 object-contain mb-2"
                    loading="lazy"
                  />
                  <span className="text-sm">{f.nome}</span>
                </div>
              ))}
            </div>
          )}

          {abaAtiva === "outros" && (
            <div className="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-2 sm:gap-3">
              {outros.map((item, idx) => (
                <div key={idx} className="bg-[#2c2c2c] border border-gray-600 rounded-md p-3 shadow-sm transition hover:border-purple-500">
                  <h3 className="text-base font-semibold text-white mb-1">{item.titulo}</h3>
                  <p className="text-gray-400 text-xs">{item.descricao}</p>
                </div>
              ))}
            </div>
          )}

          {abaAtiva === "seguranca" && (
            <div className="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-2 sm:gap-3">
              {seguranca.map((item, idx) => (
                <div key={idx} className="bg-[#2c2c2c] border border-gray-600 rounded-md p-3 shadow-sm transition hover:border-purple-500">
                  <h3 className="text-base font-semibold text-white mb-1">{item.titulo}</h3>
                  <p className="text-gray-400 text-xs">{item.descricao}</p>
                </div>
              ))}
            </div>
          )}
        </div>
      </section>

      <div className="mt-0 sm:-mt-1 overflow-hidden">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320" className="w-full h-auto block">
          <path fill="#000000" fillOpacity="1" d="M0,192L40,176C80,160,160,128,240,112C320,96,400,96,480,128C560,160,640,224,720,240C800,256,880,224,960,208C1040,192,1120,192,1200,192C1280,192,1360,192,1400,192L1440,192L1440,0L1400,0C1360,0,1280,0,1200,0C1120,0,1040,0,960,0C880,0,800,0,720,0C640,0,560,0,480,0C400,0,320,0,240,0C160,0,80,0,40,0L0,0Z" />
        </svg>
      </div>

    </>
  );
};

export default Habilidades;
